/* Global styles */
.App {
  text-align: center;
  font-family: Arial;
  overflow: hidden;
  margin: 0;
  border: 0;
  padding: 0;
}

.logo {
  position: fixed;
  top: 30px;
  right: 30px;
}

img {
  width: 100px;
  left: 0;
}

.main {
  margin-left: 160px; /* Same as the width of the sidebar */
  padding: 0px 10px 50px 10px;
  min-height: 100vh;
  background: linear-gradient(to left, aliceblue, azure);
}

/* Dropdown styles */

select {
  color: white;
  background: darkgreen;
  width: 230px;
  height: 40px;
  padding: 2px 20px 2px 10px;
  font-size: 1.1rem;
  border: 0;
}

.custom-select {
  --arrow-size: 9px;
  position: relative;
  margin: auto;
  width: 230px;
  height: 40px;
}

.custom-arrow {
  position: absolute;
  top: 0;
  right: 0;
  display: block;
  width: 30px;
  height: 100%;
  background-color: darkgreen;
  pointer-events: none;
  border-left: 1px solid white;
}

.custom-arrow::before {
  content: '';
  position: absolute;
  width: 0;
  height: 0;
  transform: translate(-8px, 22px);
  border-left: var(--arrow-size) solid transparent;
  border-right: var(--arrow-size) solid transparent;
  border-top: var(--arrow-size) solid rgba(255,255,255,0.8);
}

.custom-arrow::after {
  content: '';
  position: absolute;
  width: 0;
  height: 0;
  transform: translate(-8px, 8px);
  border-left: var(--arrow-size) solid transparent;
  border-right: var(--arrow-size) solid transparent;
  border-bottom: var(--arrow-size) solid rgba(255,255,255,0.8);
}

/* Navigation */

.nav {
  height: 100%; /* Full-height: remove this if you want "auto" height */
  width: 160px; /* Set the width of the sidebar */
  position: fixed; /* Fixed Sidebar (stay in place on scroll) */
  z-index: 1; /* Stay on top */
  top: 0; /* Stay at the top */
  left: 0;
  background-color: #111; /* Black */
  overflow-x: hidden; /* Disable horizontal scroll */
  padding-top: 20px;
  opacity: 0.92;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: top;
}

/* Nav Links */

.nav * {
  color: rgba(200,200,200, 1);
  padding: 6px 10px 6px 10px;
  text-decoration: none;
  font-size: 20px;
}

.nav *:hover {
  text-shadow: 1px 1px 1px red;
  color: red;
}

/* Class / Todo / Note cards */

.flex-container {
  display: flex;
  margin: 3vh 10vw;
  flex-wrap: wrap;
  align-items: stretch;
  align-content: flex-start;
  justify-content: left;
}

/* Individual cards */

.flex-item {
  border: 1.3px solid darkgreen;
  border-radius: 0.3rem 0.8rem;
  min-height: 10rem;
  margin: 10px 5px;
  flex-basis: 300px;
  cursor: pointer;
  max-width: 300px;
}

.flex-item:hover {
  box-shadow: 4px 4px rgba(0,255,0,0.2);
}

/* Click to view detail effect */

.flex-item > div > div:last-child {
  visibility: hidden;
  font-size: 2px;
}

.flex-item:hover > div > div:last-child {
  visibility: visible;
  font-weight: bold;
  font-size: 1.2em;
  color: red;
}

/* Title of cards */

.flex-inner-title {
  font-size: 1.5rem;
  word-wrap: break-word;
  background-color: white;
  border-bottom: 2px solid blue;
  border-radius: 0.3rem 0.8rem;
  padding: 3px;
  font-weight: bold;
}

/* Body of cards */

.flex-inner-text {
  margin: 5px 0;
}

.flex-inner-text > div{
  word-wrap: break-word;
  margin: 3px;
}

/* Buttons */

.button-main {
  background-color: lightgreen;
  width: 11.2rem;
  height: 2.5rem;
  border: 1px solid salmon;
  border-radius: 5px;
  cursor: pointer;
  font-weight: bold;
  padding: 10px 16px;
  margin: 5px;
}

/* Create class/note svgs */

.add-class,
.add-note,
.add-todo {
  margin: 10px;
  fill: green;
}

.add-class:hover,
.add-note:hover,
.add-todo:hover {
  fill: red;
}

/* Page titles */

.page-title {
  font-size: 3rem;
  padding: 35px;
  text-shadow: 1px 0 rgba(0,0,0,0.5);
}

/* Page subtitles */

.page-subtitle {
  font-size: 2.2rem;
  padding: 30px;
  text-shadow: 1px 0 rgba(0,0,0,0.5);
}

/* Page info */

.page-info {
  width: 80%;
  margin: auto;
  font-size: 1.8rem;
  padding: 10px 0 50px 0;

  display: flex;
  flex-direction: column;
}

.page-info .value {
  padding: 7px;
  font-size: 1.2rem;
  max-height: 198px;
  overflow: auto;
  margin: 20px 30px;
  word-wrap: break-word;
  position: relative;
}

.page-info .value:hover {
  transition: max-height 3s ease;
  max-height: 9999px;
}

.page-info .value::after {
  content: '';
  width: 50%;
  height: 1px;
  background: darkred;
  position: absolute;
  bottom: 0;
  right: 25%;
}

.page-info .key {
  font-size: 1.15rem;
  opacity: 0.5;
  align-self: left;
  transform: translateY(-80%);
}

/* No class/note/todo message */

.not-found {
  padding: 30px;
  font-size: 1.8rem;
}

/* Modal styles */

/* Overlay */

.modal-overlay {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  width: 100vw;
  height: 100vh;
  z-index: 1;
  background-color: rgba(255,255,255,0.2);
}

/* Title */

.modal-title {
  font-size: 1.5rem;
  margin: 50px 10px 30px 10px;
  font-weight: bold;
}

/* Content */

.modal-content {
  z-index: 1;
  position: absolute;
  top: 40px;
  left: calc(160px + 20vw);
  right: 20vw;
  bottom: 40px;
  border: 10px solid rgba(0,0,0,0.2);
  background: lightyellow;
  overflow: auto;
  border-radius: 4px;
  outline: none;
  padding: 20px;
  padding: 5px;
}

@media only screen and (max-width: 1100px) {
  .modal-content {
    left: 10vw;
    right: 10vw;
  }
}

/* Close modal button */

.close-modal {
  position: absolute;
  top: 10px;
  right: 10px;
}

.close-modal > button {
  color: red;
  font-weight: bold;
  font-size: 1.1rem;
  background: white;
  width: 33px;
  height: 33px;
  border: 3px solid darkgreen;
  border-radius: 9px;
  cursor: pointer;
}

/* Modal form */

.modal-form {
  margin: 40px 20px 10px 20px;
}

.modal-form button {
  margin-top: 30px;
}

.modal-form > div * {
  vertical-align: middle;
}

.modal label {
  display: inline-block;
  font-size: 1.2rem;
  width: 20%;
  text-align: right;
  margin: 15px;
}

.modal input {
  background-color: aliceblue;
  font-size: 1.1rem;
  border: 1px solid salmon;
  width: 54%;
  height: 35px;
  padding: 0 10px;
}

::-webkit-datetime-edit { font-size: 1.1rem; padding: 10px; }
::-webkit-datetime-edit-fields-wrapper { background: aliceblue; }
::-webkit-calendar-picker-indicator { padding: 15px; }

.modal textarea {
  border: 1px solid salmon;
  resize: none;
  background-color: aliceblue;
  font-size: 1.1rem;
  width: 54%;
  margin: 15px 0;
  height: 70px;
  padding: 10px;
}

/* Modal status message */

.modal-status * {
  text-decoration: none;
  font-weight: bold;
  color: darkred;
}

.modal-redirect {
  text-decoration: underline;
  color: purple;
}

/* Modal list (for deletion) */

.modal-list ul {
  list-style: none;
  margin: 40px 20px;
  font-size: 1.3rem;
}

.modal-list li {
  margin: 7px;
}
